import Vue from 'vue'
import VueRouter from 'vue-router'
// ctrl +左右 跳过一个单词
// shift+左右 是选中一个字母
// ctrl+shift +左右 是选中一个单词
// ctrl+ 回车 可跳转到下一行

// import Register from '@/views/Register'
// import Article from '@/views/Article'
// import Login from '@/views/Login'
// import Layout from '@/views/Layout'
// import Detail from '@/views/Detail'
// import Like from '@/views/Like'
// import User from '@/views/User'
// import Collect from '@/views/Collect'
import { getToken } from '@/utils/storage'
import { Toast } from 'vant'
// 路由懒加载 异步组件
// const Register = () => import('@/views/Register')
const Login = () => import('@/views/Login')
const Layout = () => import('@/views/Layout')
const Detail = () => import('@/views/Detail')
const Article = () => import('@/views/Article')
const Like = () => import('@/views/Like')
const User = () => import('@/views/User')
const Collect = () => import('@/views/Collect')

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/article' },
  { path: '/register', component: () => import('@/views/Register') },
  { path: '/login', component: Login },
  { path: '/detail/:id', component: Detail },
  {
    path: '/',
    component: Layout,
    children: [
      { path: '/article', component: Article },
      { path: '/like', component: Like },
      { path: '/collect', component: Collect },
      { path: '/user', component: User }
    ]
  }
]
// component: login
// 当然你也可以给component来实现路由组件的懒加载功能
// component: (resolve) => {
//       require(['./login'], resolve)
// },

const router = new VueRouter({
  routes
})

// 导航守卫  全局前置守卫
const whiteList = ['/login', '/register'] // 白名单
router.beforeEach((to, from, next) => {
  // 执行时机，每次页面跳转都会触发该函数
  // 只要注册了全局前置守卫，路由会立即瘫痪，必须调用next（）方法来放行
  // to到哪里去  from从哪里来
  // next函数，决定是否放行，next（）表示放行
  // next（‘路径’）表示强制跳转到指定路由
  // next（false）表示不放行
  // console.log(to, from)
  // next()
  const token = getToken()
  if (token) {
    next()
  } else {
    // if (to.path === '/login' || to.path === '/register') {
    //   next()
    // } else {
    //   next('/login')
    // }
    if (whiteList.includes(to.path)) {
      next()
    } else {
      Toast.fail('您还未登录，请先登录！')
      next('/login')
    }
  }
})
// 后置守卫 router.afterEach不学习
export default router
